import { Component, OnInit } from "@angular/core";
import { Customer } from "./Customer";
import { CustomerService } from "./customer.service";
import { Router, ActivatedRoute } from "@angular/router";

@Component({
  selector: "app-customer-component",
  templateUrl: "./customer-component.component.html",
  styleUrls: ["./customer-component.component.css"]
})
export class CustomerComponent implements OnInit {
  mobNumberPattern = "^((\\+91-?)|0)?[0-9]{10}$";
  showMsg: string = "";
  issuccess: boolean = false;
  alert: boolean = false;
  hide: boolean = false;
  customer: Customer = new Customer();
  id: number;
  custName: string;
  custPhone: string;
  submitFlag: boolean = true;

  constructor(
    private customerService: CustomerService,
    private router: Router,
    private route: ActivatedRoute
  ) {}

  ngOnInit() {
    this.customer.customerId = this.route.snapshot.params["id"];
    this.customer.customerName = this.route.snapshot.params["custName"];
    this.customer.customerPhone = this.route.snapshot.params["custPhone"];
    if (this.customer.customerId != null) {
      this.submitFlag = false;
    } else {
      this.submitFlag = true;
    }
  }

  createcustomer() {
    this.create();
  }

  create() {
    this.customerService.createCustomer(this.customer).subscribe(
      result => {
        console.log(result);
        this.showMsg = result["message"];
        this.issuccess = result["success"];
        console.log(this.showMsg);
        console.log(this.issuccess);
        this.alert = true;
        this.hide = false;
        this.customer = new Customer();
      },
      error => {
        console.error(error);
      }
    );
  }

  closeAlert() {
    this.hide = true;
    if (this.issuccess === true) {
      this.router.navigateByUrl("/customerlist");
    } else {
      this.customer = new Customer();
    }
  }
}
