import { Component, OnInit } from "@angular/core";
import { Customer } from "../customer-component/customer";
import { Observable } from "rxjs";
import { Router } from "@angular/router";
import { CustomerService } from "../customer-component/customer.service";

@Component({
  selector: "app-customer-list",
  templateUrl: "./customer-list.component.html",
  styleUrls: ["./customer-list.component.css"]
})
export class CustomerListComponent implements OnInit {
  customers: Observable<Customer[]>;
  customer: Customer = new Customer();

  constructor(
    private customerService: CustomerService,
    private router: Router
  ) {}

  ngOnInit() {
    this.reloadData();
  }

  reloadData() {
    this.customerService.getCustomer("All").subscribe(
      result => {
        this.customers = result["obj"];
        console.log(this.customers);
      },
      error => {
        console.error(error);
      }
    );
  }

  deleteCustomer(id: number) {
    console.log("id" + id);
    this.customerService.deleteCustomer(id).subscribe(
      data => {
        console.log(data);
        this.reloadData();
      },
      error => console.log(error)
    );
  }

  updateCustomer(customer: Customer) {
    this.customer = customer;
    console.log("id" + this.customer);
    this.router.navigate([
      "customer",
      {
        id: this.customer.customerId,
        custName: this.customer.customerName,
        custPhone: this.customer.customerPhone
      }
    ]);
  }

  customerDetails(id: number) {
    this.router.navigate(["details", id]);
  }

  getCustomer() {
    this.router.navigateByUrl("/customer");
  }
}
