import { Component, OnInit } from "@angular/core";
import { VehicleService } from "../vehicle/vehicle.service";
import { Vehicle } from "../vehicle/vehicle";
import { Weighmententry } from "./Weighmententry";
import { Customer } from "../customer-component/customer";
import { Product } from "../product/product";
import { CustomerService } from "../customer-component/customer.service";
import { ProductService } from "../product/product.service";
import { WeighmententryService } from "./Weighmententry.service";
import { DatePipe } from "@angular/common";

@Component({
  selector: "app-dashboard",
  templateUrl: "./dashboard.component.html",
  styleUrls: ["./dashboard.component.css"]
})
export class DashboardComponent implements OnInit {
  constructor(
    private vehicleService: VehicleService,
    private customerService: CustomerService,
    private productService: ProductService,
    private weighmententryService: WeighmententryService,
    private datePipe: DatePipe
  ) {}
  vehicle: Vehicle = new Vehicle();
  customer: Customer = new Customer();
  product: Product = new Product();
  weighmententry: Weighmententry = new Weighmententry();
  vehicles: any;
  customers: any;
  products: any;
  vehichleNo: string;

  ngOnInit() {}

  onVehicleChanged($event: any) {
    this.vehicle.vehicleNumber = (<HTMLInputElement>(
      document.getElementById("vehicleNumber")
    )).value;
    this.vehicleService.getVehicle(this.vehicle.vehicleNumber).subscribe(
      result => {
        this.vehicles = result["obj"];
        var stringify = JSON.parse(JSON.stringify(this.vehicles));
        //  console.log("dhivya" + JSON.stringify(stringify));
        for (var i = 0; i < stringify.length; i++) {
          if (stringify[i].vehicleNumber === this.vehicle.vehicleNumber) {
            this.weighmententry.vehicleId = stringify[i].vehicleId;
            this.weighmententry.tareWeight = stringify[i].tareWeight;
            let datedata = new Date(
              this.datePipe.transform(
                stringify[i].createdDate,
                "yyyy-MM-dd HH:mm:ss"
              )
            );
            this.weighmententry.tareDate = datedata;

            let timeData = new Date(
              this.datePipe.transform(
                stringify[i].createdDate,
                "yyyy-MM-dd HH:mm:ss"
              )
            );
            this.weighmententry.tareTime = timeData;
          }
        }
        console.log("Dhivya at date" + JSON.stringify(this.weighmententry));
      },
      error => {
        console.error(error);
      }
    );
  }

  onCustomerChanged($event: any) {
    this.customer.customerName = (<HTMLInputElement>(
      document.getElementById("customerName")
    )).value;
    this.customerService.getCustomer("All").subscribe(
      result => {
        this.customers = result["obj"];
        var stringify = JSON.parse(JSON.stringify(this.customers));
        //console.log("dhivya" + JSON.stringify(stringify));
        for (var i = 0; i < stringify.length; i++) {
          if (stringify[i].customerName === this.customer.customerName) {
            this.weighmententry.customerId = stringify[i].customerId;
          }
        }
      },
      error => {
        console.error(error);
      }
    );
  }

  onProductChanged($event: any) {
    this.product.productName = (<HTMLInputElement>(
      document.getElementById("productName")
    )).value;
    this.productService.getProduct("All").subscribe(
      result => {
        this.products = result["obj"];
        var stringify = JSON.parse(JSON.stringify(this.products));
        console.log("dhivya" + JSON.stringify(stringify));
        for (var i = 0; i < stringify.length; i++) {
          if (stringify[i].productName === this.product.productName) {
            this.weighmententry.productId = stringify[i].productId;
          }
        }
      },
      error => {
        console.error(error);
      }
    );
  }
  onChangeGrossWt($event: any) {
    this.weighmententry.grossWeight = +(<HTMLInputElement>(
      document.getElementById("grossWeight")
    )).value;
    if (this.weighmententry.grossWeight > this.weighmententry.tareWeight) {
      this.weighmententry.netWeight =
        this.weighmententry.grossWeight - this.weighmententry.tareWeight;
    }
  }

  paymentMode($event: any) {
    this.weighmententry.payMode = (<HTMLInputElement>(
      document.getElementById("payMode")
    )).value;
  }

  onChangeRate($event: any) {
    this.weighmententry.rate = +(<HTMLInputElement>(
      document.getElementById("rate")
    )).value;
    this.weighmententry.amount =
      this.weighmententry.rate * this.weighmententry.netWeight;
  }

  weighmentEntry() {
    this.weighmententry.grossDate = new Date(
      this.weighmententry.grossDate + " " + this.weighmententry.grossTime
    );
    console.log("Dhivya last" + JSON.stringify(this.weighmententry));
    this.weighmententryService
      .createWeighmententry(this.weighmententry)
      .subscribe(
        result => {
          console.log(result);
        },
        error => {
          console.error(error);
        }
      );
  }
}
