import { Component, OnInit } from "@angular/core";
import { Product } from "./product";
import { ProductService } from "./product.service";
import { Router, ActivatedRoute } from "@angular/router";

@Component({
  selector: "app-product",
  templateUrl: "./product.component.html",
  styleUrls: ["./product.component.css"]
})
export class ProductComponent implements OnInit {
  mobNumberPattern = "^((\\+91-?)|0)?[0-9]{10}$";
  showMsg: string = "";
  issuccess: boolean = false;
  alert: boolean = false;
  hide: boolean = false;
  product: Product = new Product();
  id: number;
  prodtName: string;
  prodRate: string;
  submitFlag: boolean = true;

  constructor(
    private productService: ProductService,
    private router: Router,
    private route: ActivatedRoute
  ) {}

  ngOnInit() {
    this.product.productId = this.route.snapshot.params["id"];
    this.product.productName = this.route.snapshot.params["prodName"];
    this.product.productRate = this.route.snapshot.params["prodRate"];
    if (this.product.productId != null) {
      this.submitFlag = false;
    } else {
      this.submitFlag = true;
    }
  }

  createProduct() {
    this.create();
  }

  create() {
    this.productService.createProduct(this.product).subscribe(
      result => {
        console.log(result);
        this.showMsg = result["message"];
        this.issuccess = result["success"];
        console.log(this.showMsg);
        console.log(this.issuccess);
        this.alert = true;
        this.hide = false;
      },
      error => {
        console.error(error);
      }
    );
  }

  closeAlert() {
    this.hide = true;
    if (this.issuccess === true) {
      this.router.navigateByUrl("/productlist");
    } else {
      this.product = new Product();
    }
  }
}
