import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { Observable } from "rxjs";
import { VehicleService } from "../vehicle/vehicle.service";
import { Vehicle } from "../vehicle/vehicle";
@Component({
  selector: "app-vehicle-list",
  templateUrl: "./vehicle-list.component.html",
  styleUrls: ["./vehicle-list.component.css"]
})
export class VehicleListComponent implements OnInit {
  vehicles: Observable<Vehicle[]>;
  vehicle: Vehicle = new Vehicle();

  constructor(private vehicleService: VehicleService, private router: Router) {}

  ngOnInit() {
    this.reloadData();
  }

  reloadData() {
    this.vehicleService.getVehicle("All").subscribe(
      result => {
        this.vehicles = result["obj"];
        console.log(this.vehicles);
      },
      error => {
        console.error(error);
      }
    );
  }

  deleteVehicle(id: number) {
    console.log("id" + id);
    this.vehicleService.deleteVehicle(id).subscribe(
      data => {
        console.log(data);
        this.reloadData();
      },
      error => console.log(error)
    );
  }

  updateVehicle(vehicle: Vehicle) {
    this.vehicle = vehicle;
    console.log("id" + this.vehicle);
    this.router.navigate([
      "vehicles",
      {
        id: this.vehicle.vehicleId,
        vehiNum: this.vehicle.vehicleNumber,
        vehiName: this.vehicle.vehicleName,
        weight: this.vehicle.tareWeight
      }
    ]);
  }

  vehicleDetails(id: number) {
    this.router.navigate(["details", id]);
  }

  getVehicle() {
    this.router.navigateByUrl("/vehicles");
  }
}
