import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { CustomerComponent } from "./customer-component/customer-component.component";
import { CustomerListComponent } from "./customer-list/customer-list.component";
import { ProductComponent } from "./product/product.component";
import { ProductListComponent } from "./product-list/product-list.component";
import { VehicleComponent } from "./vehicle/vehicle.component";
import { VehicleListComponent } from "./vehicle-list/vehicle-list.component";
import { DashboardComponent } from "./dashboard/dashboard.component";
const routes: Routes = [
  { path: "", redirectTo: "employee", pathMatch: "full" },
  { path: "customerlist", component: CustomerListComponent },
  { path: "customer", component: CustomerComponent },
  { path: "update/:id/:custName/:custPhone", component: CustomerComponent },
  { path: "products", component: ProductComponent },
  { path: "productlist", component: ProductListComponent },
  { path: "products/:id/:prodName/:prodRate", component: ProductComponent },
  { path: "vehicles", component: VehicleComponent },
  { path: "vehiclelist", component: VehicleListComponent },
  {
    path: "vehicles/:id/:vehiNum/:vehiName/:weight",
    component: ProductComponent
  },
  { path: "dashboard", component: DashboardComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {}
